import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, fetchEncryptStream, qryStore } from "../../../stores/Utils";

import { CheckBoxSlide_Cmpt, TextInput_Cmpt, CheckBox_Cmpt } from "../../../components";


import "./dashboard.css"
import { CloseModal } from "../../../components/DialogHRM";

const CheckBoxSlide = CheckBoxSlide_Cmpt();
const TextInput = TextInput_Cmpt();

const CheckBox = CheckBox_Cmpt();

const frmNm = "new_sub_account"

const AddClient  = ({ data, confirm }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			
			set_obs(gen16CodeId())
		}
	});

	const updFrm = (f, v, k) => {
		let frm2 = getStore(frmNm) || {};
		if(k){
			if( !frm2[k]){
				frm2[k] = {};
			}
			frm2[k][f] = v;
		}else{
			frm2[f] = v;
		}
		updStore(frmNm, frm2);
		
		set_obs(gen16CodeId())
	};


	const save  = async ()  => {
		let ffm = getStore(frmNm);

		ffm["sub_accountId"] = gen16CodeId();
		ffm["accountId"] = getStore("account_rel_subacc")?.accountId;

		
		let bdyq2 = {...getStore(qryStore())?.add_sub_acc};
		bdyq2.params = {
			businessId:  getStore("profileUser")?.businessId
		}
		bdyq2.form=ffm;
	

		const res = fetchEncryptStream(bdyq2);
		const td = await res;
		if (td) {
			updStore(frmNm, {});
			confirm && confirm();
			data?.modalID && CloseModal({id:data?.modalID})
		}
	};





	const clean = (f) => {
		updStore(frmNm, {});
		set_obs(gen16CodeId())
	};

	
	let acc = 	getStore("account_rel_subacc");


	return (
		<div style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					Agregar Sub Cuenta a
				</p>
				<div className="flexSpace"/>
				<p className="group_title addBtn" onClick={clean}>
					limpiar
				</p>
			</div>

			
			<div className="_dsplFlx acc_dtls">
				<div className="_dsplFlx"  >
					<div className="code" >
						{acc?.code} 
					</div>
					<div className="name">
						{acc?. name}
					</div>
				</div>
			</div>

			<div className="_dsplFlx ">
				<TextInput  
					init={getStore(frmNm)?.["subAccount"]} 
					label={"Sub Cuenta"} 
					width={120} 
					type={"number"}
					updChanges={(e)=>updFrm("subAccount",e)}
				/>
				<div className="flexSpace"/>
			</div>
			<TextInput  
				init={getStore(frmNm)?.["subAccountName"]} 
				label={"Nombre Sub Cuenta"} 
				width={520} 
				updChanges={(e)=>updFrm("subAccountName",e)}
			/>

			
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<div className="card">
					<button onClick={save}>
						Guardar
					</button>
				</div>
			</div>
		

			
		</div>
	);
};

export default AddClient;

